.section-works__container {
  background-color: #eee;
}

.section-works__container .title {
  color: #9ba9a9;
  text-align: center;
  -webkit-transform: translateX(-1000px);
          transform: translateX(-1000px);
  transition: -webkit-transform 750ms ease-in-out;
  transition: transform 750ms ease-in-out;
  transition: transform 750ms ease-in-out, -webkit-transform 750ms ease-in-out;
}

.section-works__container .grid {
  margin-top: 16px;
}

.section-works__container .grid .item {
  -webkit-transform: scale(0);
          transform: scale(0);
  transition: -webkit-transform 750ms ease-in-out;
  transition: transform 750ms ease-in-out;
  transition: transform 750ms ease-in-out, -webkit-transform 750ms ease-in-out;
}

@media only screen and (max-device-width: 768px) {
  .section-works__container .grid .item:first-child {
    margin-left: 16px;
  }
}

@media only screen and (min-device-width: 768px) and (max-device-width: 1366px) {
  .section-works__container .grid .item:first-child {
    margin-left: 16px;
  }
}

@media only screen and (min-device-width: 1366px) {
  .section-works__container .grid .item:first-child {
    margin-left: 32px;
  }
}

.section-works__container .grid .item:nth-last-child(n + 2) {
  margin-right: 16px;
}

.section-works__container .grid .item > div {
  background-color: #fff;
  display: block;
}

@media only screen and (max-device-width: 768px) {
  .section-works__container .grid .item .brand__responsive {
    height: 128px;
    max-height: 128px;
    width: 128px;
    max-width: 128px;
    font-size: 13.5px;
  }
}

@media only screen and (min-device-width: 768px) and (max-device-width: 1366px) {
  .section-works__container .grid .item .brand__responsive {
    height: 192px;
    max-height: 192px;
    width: 192px;
    max-width: 192px;
    font-size: 20.5px;
  }
}

@media only screen and (min-device-width: 1366px) {
  .section-works__container .grid .item .brand__responsive {
    height: 192px;
    max-height: 192px;
    width: 192px;
    max-width: 192px;
  }
}

.section-works__container .grid .item .chorus {
  content: url(data:image/png;base64,);
}

.section-works__container .grid .item .cityflo {
  content: url(data:image/png;base64,);
}

@media only screen and (max-device-width: 768px) {
  .section-works__container .grid .item .cityflo {
    padding: 12px;
  }
}

@media only screen and (min-device-width: 768px) and (max-device-width: 1366px) and (orientation: landscape) {
  .section-works__container .grid .item .cityflo {
    padding: 16px;
  }
}

@media only screen and (min-device-width: 1366px) {
  .section-works__container .grid .item .cityflo {
    padding: 20px;
  }
}

.section-works__container .grid .item .overlay {
  background-color: rgba(0, 0, 0, 0.75);
  color: #fff;
  opacity: 0;
  padding: 4px;
  position: absolute;
  transition: opacity 350ms ease-in-out;
}

.section-works__container .grid .item .overlay .meta {
  margin-left: 0;
  margin-right: 0;
  padding: 2px 4px 0 2px;
}

.section-works__container .grid .item .overlay .meta .info {
  font-weight: 300;
}

.section-works__container .grid .item .overlay .meta .brand__container {
  bottom: 0;
  left: 0;
  position: absolute;
  padding-bottom: 8px;
}

@media only screen and (max-device-width: 768px) {
  .section-works__container .grid .item .overlay .meta .brand__container {
    width: 128px;
  }
  .section-works__container .grid .item .overlay .meta .brand__container .brand {
    font-size: 24px;
  }
}

@media only screen and (min-device-width: 768px) and (max-device-width: 1366px) {
  .section-works__container .grid .item .overlay .meta .brand__container {
    width: 192px;
  }
  .section-works__container .grid .item .overlay .meta .brand__container .brand {
    font-size: 32px;
  }
}

@media only screen and (min-device-width: 1366px) {
  .section-works__container .grid .item .overlay .meta .brand__container {
    width: 192px;
  }
  .section-works__container .grid .item .overlay .meta .brand__container .brand {
    font-size: 32px;
  }
}

.section-works__container .grid .item .overlay .meta .brand__container .brand {
  color: #fff;
}

.section-works__container .grid .item .overlay .meta .brand__container .brand:hover {
  color: #2195f2;
}

.section-works__container .grid .item .overlay:hover {
  opacity: 1;
}

.section-works__container.active .title {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.section-works__container.active .item {
  -webkit-transform: scale(1);
          transform: scale(1);
}

